
import { TiLocale } from '@cloud/tiny3';
import { currencyUSD, russiaen_US } from '@cloud/tinyplus3/locale/i18n';
import { siteConfig } from './siteConfig';

export class TpConfig {
    public static init(language: string = 'en-US'): void {
        const words: any = TiLocale.getWords();
        // 设置所有语言包
        TiLocale.setWords({
            'en-US': { ...words['en-US'], ...russiaen_US, ...currencyUSD, ...siteConfig }
        });
        // 设置默认语言
        TiLocale.setLocale(language);
    }
}
